@font-face {
    font-family: 'sans-serif';
    font-style: normal;
    font-weight: normal;
    src: local('/Users/sriram/Documents/mytraining/reactProjects/covid-app/public/fonts/Archia_Regular/Archia Regular Webfont/archia-regular-webfont.woff2') format('woff2');
}
html {
    font-family: 'archia', sans-serif;
}
body {
    background-color: rgb(22, 22, 37);
}
h1 {
    font-size: 24px !important;
    font-weight: 600;
    color: #6c757d;
}
.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.image {
    width: 370px;
    margin-top: 50px;
}

@media (max-width: 770px) {
    .container {
        margin: 0 10%;
    }
    .image {
        width: 100%;
    }
}